@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

html {
  --search-border-color: #e1e7ed;
  --search-background-color: #fff;
  --search-focus-color: #f6f6f7;
  --search-text-color: #3d4e61;
  --search-shadow-color: #3d4e611a;
  --search-shadow-hover-color: #3d4e6133;
  color: var(--search-text-color);
  font-family: Avenir, sans-serif;
}

.form {
  margin-top: 20px;
  position: relative;
}

.form:after {
  content: "";
  display: block;
  position: absolute;
}

.form--big:after {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.form--small:after {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.form__input {
  border: 1px solid var(--search-border-color);
  box-shadow: 0 1px 8px var(--search-shadow-color);
  color: inherit;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: inherit;
}

.form__input::placeholder {
  color: inherit;
}

.form__input:hover {
  box-shadow: 0 3px 8px var(--search-shadow-hover-color);
}

.form__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}

.form__input--big {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.form__input--small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

